import React from "react"
import { StaticQuery, graphql, Link } from "gatsby"


const menuQuery = graphql`{
  allWordpressWpApiMenusMenusItems {
      edges {
        node {
          name
          items {
            title
            url
            object_slug
            object
          }
        }
      }
    },
    site {
      siteMetadata {
        siteURL
      }
    }
    allWordpressWpMedia {
      edges {
        node {
          slug
          source_url
        }
      }
    }
  }  
`;


  let checkType = (props, item) =>{

    //Si entro a categoria le quito
    const propsVar = props.site.siteMetadata.siteURL;
    let link ='';
    const urlString = item.url;
    console.log('1'+urlString);

    if(item.object === 'category'){
      link = urlString.replace('https://', "");
      console.log('2'+link);
      link = link.replace(propsVar, "/");
      console.log('3'+link);
    }else{
      link = item.object_slug;
      console.log('4'+link);
    }
    return link
  }

  const getMenu = (props) =>{
    return props.allWordpressWpApiMenusMenusItems.edges[1].node.items;
  }



const mainmenu =  () => (
  <StaticQuery
   query={menuQuery}
    render={props => (
      <header props={props} >

      <div className="collapsible-menu navbar  bg-faded col-12">
      <input type="checkbox" id="menu"  />
            <label htmlFor="menu">Menu</label>
            <div className="menu-content">
                <ul>
                  {
                  getMenu(props).map(item =>(
                      <li className=' nav-link' key={item.title}>
                      <Link to={checkType(props, item)} key={item.title}>  {item.title}</Link>
                      </li>
                  ))
                  }
                </ul>
            </div>
      </div>

      // </header>
    )}
  />
)

export default mainmenu;


